.hero {
    display: flex;
    justify-content: space-between;
}

.esquerda-h {
    padding: 2rem;
    padding-top: 1.5rem;
    flex:3
}

.direita-h {
    flex:1;
    position: relative;
    background-color: orange;
}

.propaganda {
    margin-top: 4rem;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    background-color: #32373b;
    text-transform: uppercase;
    color: white;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.propaganda>span {
    z-index: 2;
}

.propaganda>div {
    position: absolute;
    background-color: orange;
    width: 5.5rem;
    height: 3.3rem;
    left: 36px;
    border-radius: 3rem;
    z-index: 1;
}

.hero-text {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text>div:nth-of-type(3) {
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
}

.figuras {
    display: flex;
    gap: 2rem;
}

.figuras>div {
    display: flex;
    flex-direction: column;
}

.figuras>div>span:nth-of-type(1) {
    color: white;
    font-size: 2rem;
}

.figuras>div>span:nth-of-type(2) {
    color: grey;
    text-transform: uppercase;
}

.hero-buttons {
    display: flex;
    gap: 1rem;
    font-weight: normal;
}

.hero-buttons>:nth-child(1) {
    color: white;
    background-color: orange;
    width: 8rem;
}

.hero-buttons>:nth-child(2) {
    color: white;
    background-color: transparent;
    width: 8rem;
    border: 2.3px solid orange;
}

.direita-h>.btn {
    position: absolute;
    right: 3rem;top: 2rem;color: black;
}

.heart-rate {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #3c3f45;
    width: fit-content;
    padding: 1rem;
    align-items: flex-start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}

.heart-rate>img {
    width: 2rem;
}

.heart-rate>:nth-child(2) {
    color: white;
}

.heart-rate>:nth-child(3) {
    color: white;
    font-size: 1.5rem;
}

.hero-image {
    position: absolute;
    top: 9.5rem;
    right: 8rem;
    width: 23rem;
}

.hero-image-back {
    position: absolute;
    top: 4rem;
    right: 19rem;
    z-index: -1;
    width: 15rem;
}

.hero-blur {
    width: 22rem;
    height: 30rem;
    left: 0;
}

.calories {
    display: flex;
    gap: 2rem;
    border-radius: 5px;
    background-color: var(--caloryCard);
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 32rem;
    right: 28rem;
}

.calories>img {
    width: 3rem;
}

.calories>div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.calories>div>:nth-child(1) {
    color: rgb(226, 226, 226);
}

.calories>div>:nth-child(2) {
    color: white;
    font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
    .hero {
        flex-direction: column;
    }

    .hero-blur {
        width: 14rem;
    }

    .propaganda {
        margin-top: 0;
        font-size: small;
        align-self: center;
        transform: scale(0.8);
    }

    .hero-text {
        position: relative;
        font-size: xx-large;
        align-items: center;
        justify-content: center;
    }

    .hero-text .center-span {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
    }

    .hero-text>div:nth-child(3) {
        font-size: small;
        font-weight: 200;
        letter-spacing: 1px;
        text-align: center;
        margin-bottom: 20px;
    }

    .hero-buttons {
        justify-content: center;
    }

    .figuras>div>span:nth-of-type(1) {
        font-size: large;
        
    }

    .figuras>div>span:nth-of-type(2) {
        font-size: x-small;
    }

    .direita-h {
        position: relative;
        background: none;
    }

    .heart-rate {
        left: 1rem;
        top: 2rem;
    }

    .calories {
        position: relative;
        top: 5rem;
        left: 2rem;
    }

    .calories>img {
        width: 2rem;
    }

    .calories>div>:nth-child(2) {
        color: white;
        font-size: 1rem;
    }

    .hero-image {
        position: relative;
        width: 15rem;
        left: 7rem;
        top: 4rem;
        align-self: center;
    }

    .hero-image-back {
        width: 15rem;
        left: 2rem;
        top: 0rem;
    }
}